import { defineComponent, ref, onMounted } from 'vue';
import ButtonComponent from '../components/ButtonComponent.vue';
import SidebarComponent from '../components/SidebarComponent.vue';
import NavBar from '../components/NavBar.vue';
import { useRouter } from 'vue-router';
import { listUsers } from '../api/authService';
export default defineComponent({
    name: "Dashboard",
    components: {
        ButtonComponent,
        SidebarComponent,
        NavBar,
    },
    data() {
        return {
            filteredMenuItems: [], // Filtered based on role
            username: "Invité",
            role: "User", // Rôle par défaut
        };
    },
    setup() {
        const router = useRouter();
        // Récupérer les données utilisateur depuis localStorage
        const username = ref(localStorage.getItem("name") || "Invité");
        const role = ref(localStorage.getItem("role") || "User");
        const entities = ref([]);
        const entityTitle = ref("");
        const userId = ref(localStorage.getItem("id") || "1");
        //const clientID = ref("");
        //const ContractId = ref("");
        // Définir les menus spécifiques à chaque rôle
        const menuItems = ref([
            { name: 'Tableau de Bord', link: '/dashboard', active: false, visibleTo: ['Admin', 'Client', 'Manager', 'Utilisateur'] },
            { name: 'Liste des Utilisateurs', link: '/liste', active: false, visibleTo: ['Admin', 'Client', 'Manager'] },
            { name: "Mon Profil", link: '/profile', active: true, visibleTo: ['Admin', 'Client', 'Manager', 'Utilisateur'] },
        ]);
        // Filtrer les éléments du menu en fonction du rôle de l'utilisateur
        const filteredMenuItems = menuItems.value.filter(item => item.visibleTo.includes(role.value));
        onMounted(async () => {
            try {
                let response;
                const entityType = role.value;
                switch (entityType) {
                    case "Admin":
                        entityTitle.value = "Admin";
                        break;
                    case "Client":
                        entityTitle.value = "Client";
                        break;
                    case "Manager":
                        entityTitle.value = "Manager";
                        break;
                    case "Utilisateur":
                        entityTitle.value = "Utilisateur";
                        break;
                    default:
                        console.warn(`Type d'entité inconnu: ${entityType}`);
                        return;
                }
                console.log(entityTitle.value);
                // Les admins voient tous les utilisateurs, les autres rôles ne voient que ceux ayant le même managerID
                response = await listUsers({ role: entityTitle.value, id: userId.value }); // L'admin récupère tous les utilisateurs
                console.log(response);
                if (response && Array.isArray(response)) {
                    entities.value = response;
                }
                else {
                    console.warn("Aucune donnée reçue pour la liste des entités.");
                }
            }
            catch (error) {
                console.error("Une erreur est survenue lors de la récupération des données.", error);
            }
        });
        const editEntity = (entityId) => {
            console.log("Edit entity:", entityId);
        };
        const deleteEntity = (entityId) => {
            console.log("Delete entity with ID:", entityId);
        };
        const editContract = (ContratId) => {
            console.log('Editing client:', ContratId);
            // Implementation
        };
        const deleteContract = (ContratId) => {
            console.log('Editing client:', ContratId);
            // Implementation
        };
        const logout = () => {
            localStorage.removeItem("username");
            localStorage.removeItem("role");
            localStorage.removeItem('token');
            router.push({ name: 'Login' });
        };
        return {
            username, role, menuItems, filteredMenuItems,
            entities, entityTitle, editEntity, userId,
            deleteEntity, editContract, deleteContract,
            logout
        };
    },
});
